import demo from '@/views/demo'
import home from '@/views/home'
import singleton from '@/docs/patterns/singleton.md'
import strategie from '@/docs/patterns/strategie.md'
import proxy from '@/docs/patterns/proxy.md'

const routes = [
  {
    path: '/',
    name: '',
    component: home
  },
  {
    path: '/patterns',
    name: '',
    component: demo,
    redirect: '/patterns/singleton',
    children: [
      { path: '/patterns/singleton', name: '', component: singleton },
      { path: '/patterns/strategie', name: '', component: strategie },
      { path: '/patterns/proxy', name: '', component: proxy }
    ]
  }
]

export default routes



