</section></template>

<script>
export default {
  data () {
    return {
      showList:[]
    }
  },
  methods: {
    proxImage () {
      let myImage = (() => {
        let imgNode = document.createElement('img')
        document.getElementById('box').appendChild(imgNode)
        return function (src) {
          imgNode.src = src
        }
      })()

      let proxyImage = (() => {
        let img = new Image()
        img.onload = function () {
          myImage(this.src)
        }
        return function (src) {
          myImage('http://www.w3school.com.cn/i/eg_chinarose.jpg')
          img.src = src
        }
      })()

      proxyImage('http://www.w3school.com.cn/i/eg_tulip.jpg')
    },
    uploadFile () {
      let target = event.target
      let synchronousFile = (id)=> {
        this.showList.push(`${id}.....`)
      }

      let proxySynchronousFile = (function () {
        let cache = [], // ID
            timer // 
        return function (id) {
          cache.push(id)
          if (timer) { // 
            return
          }
          timer = setTimeout(function () {
            synchronousFile(cache.join(',')) // 2 ID 
            clearTimeout(timer) // 
            timer = null
            cache.length = 0 // ID 
          }, 2000)
        }
      })()
      if (target.checked) {
        proxySynchronousFile(target.value)
      }
    }
  },
  mounted () {
    this.proxImage()
  }
}
</script>


